import React, { Component } from 'react';
import { DropdownButton, MenuItem} from 'react-bootstrap';
import DogsContainer from './DogsContainer';
import './FilteredList.css';
import Bluedogpaper from './Images/bluedog.jpg';
import Puplogo from './Images/puplogorightcolor.png';

var backstyle = {
  backgroundImage: 'url('+Bluedogpaper+')'
};

class FilteredList extends Component {
  constructor(props) {
    super(props);

    this.state = {
      search: "",
      type: "Cuteness",
      breed: "Breed Group",
      sortBy: "Name"
    };
    this.props.items.sort(function(a,b) {
      var nameA = a.name.toUpperCase();
      var nameB = b.name.toUpperCase();
      if (nameA < nameB) {
        return -1;
      }
      else if (nameA > nameB) {
        return 1;
      }
      return 0; });
  }



  // Sets the state whenever the user types on the search bar
  onSearch = (event) => {
    this.setState({search: event.target.value.trim().toLowerCase()});
  }
  filterItem = (item) => {
    // Checks if the current search term is contained in this item
    // TODO: Add condition to check item’s type
    if ((item.type===this.state.type) || (this.state.type === 'Cuteness'))
      if ((item.breed===this.state.breed) || (this.state.breed === 'Breed Group'))
        return item.name.toLowerCase().search(this.state.search) !== -1;
  }
  /* TODO: Add an event handling method for when an item in dropdown is selected
  Per the DropdownButton documentation, this function should take in an eventKey and
  event
  */
  onSelectMenuType = (eventKey) => {
    this.setState({type: eventKey
    });
  }


  onSelectMenuBreed = (eventKey) => {
    this.setState({breed: eventKey
    });
  }

  onSelectMenuSort = (eventKey) => {
    this.setState({sortBy: eventKey});
    if (eventKey === "Name") {
      this.props.items.sort(function(a,b){
        var nameA = a.name.toUpperCase();
        var nameB = b.name.toUpperCase();
        if (nameA < nameB) {
          return -1;
        }
        else if (nameA > nameB) {
          return 1;
        }
        return 0; });
    }
    else if (eventKey === "Breed Group") {
      this.props.items.sort(function(a,b){
        var breedA = a.breed.toUpperCase();
        var breedB = b.breed.toUpperCase();
        if (breedA < breedB) {
          return -1;
        }
        else if (breedA > breedB) {
          return 1;
        }
        return 0; });
    }
    else if (eventKey === "Rating (High to Low)") {
      this.props.items.sort(function(a,b){
        return -(a.rating-b.rating);
      })
    }
    else if (eventKey === "Rating (Low to High)") {
      this.props.items.sort(function(a,b) {
        return a.rating-b.rating;
      })
    }

  }

  render() {
    return (
      <div className="filter-list">
        <div className = "head">
          <div className="headers">
            <img className = "puplogo" src = {Puplogo} alt = "puppy icon"/>
            <h1 id = "titleof">Puppah Sortah</h1>
            <img className = "puplogo" src = {Puplogo} alt = "puppy icon"/>
          </div>
          <hr className = "hr"/>
          <div id = "heading">
            <div id = "cute-pair">
              <h2 id = "cuteness" className = "headfilt">Cuteness</h2>
              <DropdownButton id="typeDropdown" title={this.state.type}>
                <MenuItem eventKey="Cuteness" onSelect={this.onSelectMenuType}>All</MenuItem>
                <MenuItem eventKey="I Pet Now" onSelect={this.onSelectMenuType}>I Pet Now</MenuItem>
                <MenuItem eventKey="Mlem" onSelect={this.onSelectMenuType}>Mlem</MenuItem>
                <MenuItem eventKey="SO CUDDLE" onSelect={this.onSelectMenuType}>SO CUDDLE</MenuItem>
              </DropdownButton>
            </div>
            <div id = "group-pair">
              <h2 id = "group" className = "headfilt">Breed Group</h2>
              <DropdownButton id="breedgroupDropdown" title={this.state.breed}>
                <MenuItem eventKey="Breed Group" onSelect={this.onSelectMenuBreed}>All</MenuItem>
                <MenuItem eventKey="Herding" onSelect={this.onSelectMenuBreed}>Herding</MenuItem>
                <MenuItem eventKey="Hound" onSelect={this.onSelectMenuBreed}>Hound</MenuItem>
                <MenuItem eventKey="Sporting" onSelect={this.onSelectMenuBreed}>Sporting</MenuItem>
                <MenuItem eventKey="Non-Sporting" onSelect={this.onSelectMenuBreed}>Non-Sporting</MenuItem>
                <MenuItem eventKey="Terrier" onSelect={this.onSelectMenuBreed}>Terrier</MenuItem>
                <MenuItem eventKey="Toy" onSelect={this.onSelectMenuBreed}>Toy</MenuItem>
                <MenuItem eventKey="Working" onSelect={this.onSelectMenuBreed}>Working</MenuItem>
              </DropdownButton>
            </div>
            <div id = "group-pair">
              <h2 id = "sortby" className = "headfilt">Sort By</h2>
              <DropdownButton id="sortbyDropdown" title={this.state.sortBy}>
                <MenuItem eventKey="Breed Group" onSelect={this.onSelectMenuSort}>Breed Group</MenuItem>
                <MenuItem eventKey="Name" onSelect={this.onSelectMenuSort}>Name</MenuItem>
                <MenuItem divider/>
                <MenuItem eventKey="Rating (High to Low)" onSelect={this.onSelectMenuSort}>Rating (High to Low)</MenuItem>
                <MenuItem eventKey="Rating (Low to High)" onSelect={this.onSelectMenuSort}>Rating (Low to High)</MenuItem>
              </DropdownButton>
            </div>
            <div id = "search-pair">
              <h2 id = "search" className = "headfilt">Search</h2>
              <input type="text" placeholder="Type Here" onChange={this.onSearch}/>
            </div>
          </div>
        </div>
        <hr className = "hr" id = "two"/>
        <div className = "backgroundwrapper" style={backstyle}>
          <DogsContainer id = "dogscont" items={this.props.items.filter(this.filterItem)} />
        </div>

      </div>
    );
  }
}
export default FilteredList;
