import React, { Component } from 'react';
import './DogItem.css';
import {Modal} from 'react-bootstrap';
import {Button} from 'react-bootstrap';
class DogItem extends Component {
  constructor(props, context) {
    super(props, context);
    this.openModal = this.openModal.bind(this);
    this.handleClose = this.handleClose.bind(this);

    this.state = {
      showModal: false
    }
  }

  openModal() {
    this.setState({showModal: true});
  }
  handleClose() {
    this.setState({showModal: false});
  }

  render() {
    var item = this.props.dog;
    return (
      <div>
        <div key={item.name} className = "dog" onClick = {this.openModal}>
          <h1> {item.name} </h1>
          <img className = "image" src = {item.imlink} alt = {item.name}/>
          <div className = "desc">
            <div className = "ratings">
              <h2 className = "ratingtitle">Rating</h2>
              <h2 className = "rating">{item.rating.toString()}/10</h2>
            </div>
            <div className = "breeds">
              <h2 className = "breedtitle">Breed Group</h2>
              <h2 className = "breed">{item.breed}</h2>
            </div>
          </div>
        </div>
        <Modal show={this.state.showModal} onHide={this.handleClose}>
            <Modal.Header closeButton>
              <Modal.Title className = "modalTitle">{item.name}</Modal.Title>
            </Modal.Header>
            <Modal.Body>
              <img className = "imageModal" src = {item.imlink} alt = {item.name}/>
              <div className = "descModal">
                <div className = "ratingsModal">
                  <h2 className = "ratingtitle">Rating</h2>
                  <h2 className = "rating">{item.rating.toString()}/10</h2>
                </div>
                <div className = "breedsModal">
                  <h2 className = "breedtitle">Breed Group</h2>
                  <h2 className = "breed">{item.breed}</h2>
                </div>
              </div>
              <h1 className = "modalBlurb">{item.blurb}</h1>
            </Modal.Body>
            <Modal.Footer>
              <Button onClick={this.handleClose}>Close</Button>
            </Modal.Footer>
          </Modal>
      </div>
    )
  }
}

export default DogItem;
